"use client";

import React from "react";
import { IMaskInput } from "react-imask";

interface PhoneInputProps
  extends Omit<React.InputHTMLAttributes<HTMLInputElement>, "onChange" | "value"> {
  value: string;
  onChange: (val: string) => void;
  id?: string;
  className?: string;
}

const PhoneInput = React.forwardRef<HTMLInputElement, PhoneInputProps>(
  ({ value, onChange, id, className, ...rest }, ref) => {
    return (
      <IMaskInput
        // mask pattern: +998 00 000 0000
        mask={"+998 00 000 0000"}
        // define "0" as a digit
        definitions={{ "0": /\d/ }}
        // keep the mask in the value
        unmask={false}
        // character for empty slots
        placeholderChar={"_"}
        // controlled value
        value={value}
        // fires on every valid change
        onAccept={(val: string) => onChange(val)}
        // forward the ref to the underlying input
        inputRef={ref}
        // preserve id & styling
        id={id}
        className={className}
        {...rest}
      />
    );
  }
);

PhoneInput.displayName = "PhoneInput";
export default PhoneInput;
